h4 {
    &.archived-listing {
        color: #fff;
        margin-top: 27px;
        text-align: center;
    }
}
.progress-archived {
    height: 25px;
    margin: auto;
    margin-bottom: 10px;
    .progress-bar {
        height: 100%;
        justify-content: center;
        align-items: center;
        display: flex;
    }
}
.reactivate {
    margin-bottom: 14px !important;
    color: red;
}
.expire-archived {
    font-size: 12px;
}